@import "../../global.scss";

.works{
    background-color: rgb(245, 2, 2);
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    overflow: hidden;

    h1{
        margin-bottom: 525px;
        color: white;
        font-size: 50px;
        z-index: 1;

        @include mobile {
            font-size: 50px;
        }
    }

    .icon{
        margin-bottom: 35px;
        height: 50px;
        position: absolute;
        cursor: pointer;

        @include mobile {
            display: none;
        }

        &.left{
            left: 100px;
            transform: rotateY(180deg);

        }
        &.right{
            right: 100px;
        }

        &:hover{
            color: white;
          }

    }


    .slider{
        margin-bottom: 35px;
        height: 350px;
        display: flex;
        position: absolute;
        left: 0;
        transition: all 1s ease-in-out;

        @include mobile {
            margin-top: 100px;
            height: 100vh;
            flex-direction: column;
            justify-content: center;
        }

        .container{
            width: 100vw;
            display: flex;
            align-items: center;
            justify-content: center;
            
            .item{
                width: 700px;
                height: 100%;
                background-color: white;
                border-radius: 20px;
                display: flex;
                align-items: center;
                justify-content: center;

                @include mobile {
                    width: 80%;
                    height: 150px;
                    margin: 15px 0;
                }

                .left{
                    flex: 4;
                    height: 80%;
                    display: flex;
                    align-items: center;
                    justify-content: center;

                    .leftContainer{
                        width: 90%;
                        height: 70%;
                        display: flex;
                        flex-direction: column;
                        justify-content: space-between;

                        .imgContainer{
                            width: 40px;
                            height: 40px;
                            border-radius: 50%;
                            background-color: black;
                            display: flex;
                            align-items: center;
                            justify-content: center;

                            @include mobile {
                                width: 20px;
                                height: 20px;
                            }

                            img{
                                width: 25px;

                                @include mobile {
                                    width: 15px;
                                }
                            }
                        }
                        h2{
                            font-size: 20px;

                            @include mobile{
                                font-size: 13px;
                            }
                        }
                        p{
                            font-size: 13px;
                            @include mobile{
                                display: none;
                            }
                        }
                        span{
                            font-size: 12px;
                            font-weight: 600;
                            text-decoration: underline;
                            cursor: pointer;
                        }
                    }
                }
                .right{
                    flex: 8;
                    height: 100%;
                    position: relative;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    overflow: hidden;
                    
                    @include mobile {
                        width: 15px;
                        height: 150px;
                        margin: auto 10px;
                    }

                    .imgContainer{
                        width: 300px;
                        height: 300px;
                        border-radius: 50%;
                        background-color: $mainColor;
                        display: flex;
                        align-items: center;
                        justify-content: center;

                        @include mobile {
                            width: 100px;
                            height: 100px;
                        }

                        img{
                            width: 300px;
                            height: 200px;
                          

                            @include mobile {
                                width: 100px;
                                height: 100px;
                            }
                        }
                    }
                    
                }
            }
        }
    }
}